@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700;800&family=Roboto:wght@500;700;900&display=swap');

    @import "statics";
    @import "animation";
    @import "header";
    @import "home";
    @import "work";
    @import "timeline";
    @import "services";
    @import "testimonial";
    @import "contact";
    @import "footer";
    @import "mediaquery";
   

 *{
    margin: 0%;
    padding: 0%;
    font-family: $text2;
    box-sizing: border-box;
    scroll-behavior: smooth;
 }

 a{
    text-decoration: none;
 }

 body{
    background-color: $color1;
    overflow-x: hidden;
 }

 .disableBtn {
   color: $color6 !important;
   background-color: rgb(191, 191, 191) !important;
   cursor: not-allowed !important;
 }

 #customMessage{
   font: 900 2rem $text2;
   width: 100%;
   height: 100vh;
   display: grid;
   place-items: center;
 }